.gpt3_footer
{
    color: white;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.gpt3_footer-heading
{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;

}

.gpt3_footer-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;
    letter-spacing: -0.04rem;
}


.gpt3_footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}


.gpt3_footer-btn p{
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
    word-spacing: 2px;
}

.gpt3_footer-links
{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
}

.gpt3_footer-links div{
    width: 250px;
    margin: 1rem;

}

.gpt3_footer-links_logo
{
    display: flex;
    flex-direction: column;
}

.gpt3_footer-links_logo img{
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;

}

.gpt3_footer-links_logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
}

.gpt3_footer-links_div
{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3_footer-links_div h4{
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    margin-bottom: 1rem;
}

.gpt3_footer-links_div p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt3_footer-copyright
{
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}


.gpt3_footer-copyright p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: #FFFFFF;
}


@media screen and (max-width: 850px)
{
    .gpt_footer-heading h1
    {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px)
{
    .gpt_footer-heading h1
    {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3_footer-links _div{
        margin: 1rem 0;
    }

    .gpt3_footer-heading h1{
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3_footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}