.gpt3_blog-container_article
{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt3_blog-container_article-image{
    width: 100%;
    height: 100%;
}

.gpt3_blog-container_article img{
    width: 100%;
    height: 100%;
}

.gpt3_blog-containr_article-content
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt3_blog-containr_article-content p{
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: #FFFFFF;
}

.gpt3_blog-containr_article-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    color: #FFFFFF;
    margin-bottom: 5rem;
    cursor: pointer;
}

.gpt3_blog-containr_article-content p:last-child{
    cursor:pointer;

}



@media screen and (max-width: 550px){
    .gpt3_blog-container_article-content{
        font-size: 18px;
        line-height: 25px;
        
    }
}